const DEFAULT_ROW_WIDTH = '100%';
const DEFAULT_LAST_ROW_WIDTH = '60%';

export default {
	props: {
		loading: {
			type: Boolean,
			default: true
		},
		imgTitle: {
			type: Boolean,
			default: false
		},
		nameRow: {
			type: Number,
			default: 1
		},
		flexType: {
			type: String,
			default: 'flex-start' // center	居中	√		space-between	两端对齐	√		space-around	子元素拉手分布	√		flex-start	居左		flex-end	居右
		},
		showAvatar: {
			type: Boolean,
			default: true
		},
		avatarSize: {
			type: String,
			default: '50px'
		},
		avatarShape: {
			type: String,
			default: 'round' // square | round
		},
		showTitle: {
			type: Boolean,
			default: false
		},
		titleWidth: {
			type: String,
			default: '40%'
		},
		row: {
			type: Number,
			default: 3
		},
		animate: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {};
	},
	computed: {
		rowList() {
			let list = [];
			for (let i = 0; i < this.row; i++) {
				list.push({
					width: i === this.row - 1 && i !== 0 ? DEFAULT_LAST_ROW_WIDTH : DEFAULT_ROW_WIDTH
				});
			}
			return list;
		}
	}
};
